import React from 'react';
import { collectTags } from './collectTags';
export class SelectByTagComponent extends React.Component {
    constructor(props) {
        super(props);
        this.state = { tags: ['-'] };
    }
    render() {
        const dropdownList = [];
        for (let i = 0; i < this.state.tags.length; i++) {
            dropdownList.push(React.createElement("option", { key: i, value: this.state.tags[i] }, this.state.tags[i]));
        }
        return React.createElement("div", { className: "jp-HTMLSelect jp-DefaultStyle jp-Notebook-toolbarCellTypeDropdown" },
            React.createElement("select", null, dropdownList));
    }
    ;
    componentDidMount() {
        const { model } = this.props;
        model.contentChanged.connect((sender, args) => {
            let notebookTags = collectTags(sender);
            if (notebookTags.length === 0)
                notebookTags = ['-'];
            this.setState({ tags: notebookTags });
        });
    }
}
